import React from "react";
import axios from "axios"
import Info from "../Info";
import { useCart } from "../../hooks/useCart";

import styles from './Drawer.module.scss'

const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms))

const Drawer = ({
  onClickClose,
  items,
  onRemove,
  opened
}) => {
  const { cartItems, setCartItems, totalPrice, tax } = useCart()
  const [orderId, setOrderId] = React.useState(null)
  const [isOrderComplete, setIsOrderComplete] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)


  const onClickOrder = async () => {
    try {
      setIsLoading(true)
      const { data } = await axios.post('https://6249a8a6852fe6ebf87e0a0f.mockapi.io/orders', {
        items: cartItems
      })
      setOrderId(data.id)
      setIsOrderComplete(true)
      setCartItems([])

      for (let i = 0; i < cartItems.length; i++) {
        const item = cartItems[i];
        axios.delete('https://6249a8a6852fe6ebf87e0a0f.mockapi.io/cart/' + item.id)
        await delay(1000)
      }

    } catch (error) {
      alert('Ошибка при создании заказа :(')
    }
    setIsLoading(false)
  }
  return (
    <div className={`${styles.overlay} ${opened ? styles.overlayVisible : ''}`}>
      <div className={styles.drawer}>
        <h2 className="d-flex justify-between mb-30">
          Корзина
          <img
            className="cu-p"
            src="/img/btn-remove.svg" alt="Close"
            onClick={onClickClose}
          />
        </h2>
        {
          items.length > 0 ? (
            <div className="d-flex flex-column flex">
              <div className="items flex">
                {items.map((obj) => (
                  <div className="cartItem d-flex align-center mb-20" key={obj.id}>
                    <div
                      style={{ backgroundImage: `url(${obj.imageUrl})` }}
                      className="cartItemImg"
                    ></div>
                    <div className="mr-20 flex">
                      <p className="mb-5">{obj.title}</p>
                      <b>{obj.price} руб.</b>
                    </div>
                    <img className="removeBtn"
                      src="/img/btn-remove.svg"
                      alt="Remove"
                      onClick={() => onRemove(obj.id)}
                    />
                  </div>
                ))}
              </div>
              <div className="cartTotalBlock">
                <ul>
                  <li>
                    <span>Итого:</span>
                    <div></div>
                    <b>{totalPrice} руб. </b>
                  </li>
                  <li>
                    <span>Налог 5%:</span>
                    <div></div>
                    <b>{tax} руб. </b>
                  </li>
                </ul>
                <button className="greenButton" onClick={onClickOrder} disabled={isLoading}>
                  Оформить заказ <img src="/img/arrow.svg" alt="Arrow" />
                </button>
              </div>
            </div>
          ) : (
            <Info
              title={isOrderComplete ? "Заказ оформлен" : "Корзина пустая"}
              description={
                isOrderComplete
                  ? `Ваш заказ ${orderId} скоро будет передан курьерской доставке`
                  : 'Добавьте хотя бы одну пару кроссовок, чтобы сделать заказ.'
              }
              image={isOrderComplete ? '/img/complete-order.jpg' : '/img/empty-cart.jpg'} />
          )}
      </div>
    </div>
  );
}

export default Drawer;
